/*
========================================
text-indent
----------------------------------------
usage:
  .text-indent-[key]
----------------------------------------
output:
  align: [value];
----------------------------------------
example:
  .text-indent-0 {
    text-ident: 0; }
----------------------------------------
*/

$u-text-indent: (
  text-indent: (
    base: 'text-indent',
    modifiers: null,
    values: map-collect(
      get-palettes($text-indent-palettes),
      $text-indent-manual-values
    ),
    settings: $text-indent-settings,
    property: 'text-indent',
    type: 'utility',
  )
);
