/*
========================================
text-decoration
----------------------------------------
usage:
  .text-[key]
----------------------------------------
output:
  text-decoration: [value]
----------------------------------------
example:
  .text-no-underline {
    text-decoration: none; }
----------------------------------------
*/

$u-text-decoration: (
  text-decoration: (
    base: 'text',
    modifiers: null,
    values: map-collect(
      get-palettes($text-decoration-palettes),
      $text-decoration-manual-values
    ),
    settings: $text-decoration-settings,
    property: 'text-decoration',
    type: 'utility',
  )
);
