iframe:focus,
[href]:focus,
[tabindex]:focus,
[contentEditable=true]:focus {
  outline: 0.25rem solid #2491ff;
  outline-offset: 0;
}

.usa-focus {
  outline: 0.25rem solid #2491ff;
  outline-offset: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  font-feature-settings: 'kern' 1;
  font-kerning: normal;
  font-family: Lato, Helvetica Neue, Helvetica, Roboto, Arial, sans-serif;
  font-size: 100%;
}

cite,
var,
address,
dfn {
  font-style: normal;
}

body {
  background-color: white;
  color: #1b1b1b;
  overflow-x: hidden;
}

.usa-sr-only {
  position: absolute;
  left: -999em;
}

.jcc-steps {
  list-style: none;
  padding-left: 0;
  margin-left: 0;
}

.jcc-steps__step {
  counter-increment: step-counter;
  position: relative;
  max-width: 72ex ;
  line-height: 1.50829 ;
  padding-left: 3.5rem;
}

.jcc-steps__step::before {
  position: absolute;
  top: -7px;
  right: 0;
  bottom: 0;
  left: -7px;
  padding: .625rem;
  background-color: #005ea2;
  border: 3px solid white;
  border-radius: 50%;
  color: white;
  content: counter(step-counter);
  font-size: 1.125rem;
  height: 3rem;
  line-height: 1.125;
  text-align: center;
  width: 3rem;
}

.jcc-steps__step:after {
  content: "";
  display: block;
  width: 0;
  height: 100%;
  border: 1px solid #f0f0f0;
  position: absolute;
  top: 0;
  left: 1rem;
  z-index: -1;
}

.jcc-steps__step:last-of-type:after {
  height: 0;
}

